import { Component, OnInit } from '@angular/core';
import {HttpClient, HttpClientModule, HttpHeaders, HttpParams} from "@angular/common/http";
import {Observable} from "rxjs";
import {Post} from "./API";
import { map } from 'rxjs/operators';

@Component({
  selector: 'app-home-page',
  templateUrl: './home-page.component.html',
  styleUrls: ['./home-page.component.css']
})
export class HomePageComponent implements OnInit {

  readonly ROOT_URL = 'https://jsonplaceholder.typicode.com/posts';
  readonly ROOT_URL1 = 'http://51.145.47.43/api/values';

  page: number;
  posts: Observable<any>;
  title: Observable<string>;
  subtitle: Observable<any>;
  article: Observable<any>;
  day: Observable<any>;
  month: Observable<any>;
  url1: Observable<any>;
  url2: Observable<any>;
  newPost: Observable<any>;

  constructor(private http: HttpClient) {
    this.page = 1;
    this.getTitle();
  }

  getPosts() {
    this.posts = this.http.get<Post>(this.ROOT_URL1)
  }
  getNext() {
    this.page++;
    this.title = this.http.get<Post>(this.ROOT_URL1 + '/' + this.page).pipe(map(post => post.title))
    this.getSubtitle();
    this.getArticle();
    this.getDay();
    this.getMonth();
    this.getURL1();
    this.getURL2();
  }
  getPrev() {
    this.page--;
    this.title = this.http.get<Post>(this.ROOT_URL1 + '/' + this.page).pipe(map(post => post.title))
    this.getSubtitle();
    this.getArticle();
    this.getDay();
    this.getMonth();
    this.getURL1();
    this.getURL2();
  }
  getTitle() {
    this.title = this.http.get<Post>(this.ROOT_URL1 + '/' + this.page).pipe(map(post => post.title))
    this.getSubtitle();
    this.getArticle();
    this.getDay();
    this.getMonth();
    this.getURL1();
    this.getURL2();
  }
  getSubtitle() {
    this.subtitle = this.http.get<Post>(this.ROOT_URL1 + '/' + this.page).pipe(map(post => post.subtitle))
  }
  getArticle() {
    this.article = this.http.get<Post>(this.ROOT_URL1 + '/' + this.page).pipe(map(post => post.article))
  }
  getDay() {
    this.day = this.http.get<Post>(this.ROOT_URL1 + '/' + this.page).pipe(map(post => post.day))
  }
  getMonth() {
    this.month = this.http.get<Post>(this.ROOT_URL1 + '/' + this.page).pipe(map(post => post.month))
  }
  getURL1() {
    this.url1 = this.http.get<Post>(this.ROOT_URL1 + '/' + this.page).pipe(map(post => post.urlone))
  }
  getURL2() {
    this.url2 = this.http.get<Post>(this.ROOT_URL1+ '/' + this.page).pipe(map(post => post.urltwo))
  }

  ngOnInit() {
  }

}
